.bg {
  background: url(./bg-morning.svg) no-repeat center center;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-self: center;
}
.rock {
  height: 50px;
  width: 50px;
}

.island {
  display: flex;
  flex-direction: row;
  align-self: flex-end;
  height: 200px;
  width: 200px;
  position: relative;
  bottom: 10px;
  right: 200px;
}
.island2 {
  display: flex;
  flex-direction: row;
  align-self: flex-end;
  height: 200px;
  width: 200px;
  position: relative;
  left: 450px;
}
.island3 {
  bottom: 60px;
  display: flex;
  flex-direction: row;
  align-self: flex-end;
  height: 200px;
  width: 200px;
  position: relative;
  left: 350px;
  /* bottom: 60px; */
}
.monster {
  display: flex;
  flex-direction: row;
  align-self: center;
  height: 120px;
  width: 100px;
  margin-left: 100px;
  position: relative;

}
html, body {
  height: 100vh;
  width: 100vw;
  
}
.rock {
position: relative;
align-self: center;
left: 200px;
}
.speed {
  min-width: 50px;
  min-height: 50px;
  /* margin: 50px; */
  font-family: "Roboto";
  font-size: 50px;
  border-bottom: 10px solid aqua;

}

.feedback{
  display: flex;
  flex: 1;
  flex-direction: column-reverse;
  color: white;
  background-color: #2d3436;
  background-image: linear-gradient(315deg, #2d3436 0%, #000000 74%);
  background-color: #166d3b;
  background-image: linear-gradient(147deg, #166d3b 0%, #000000 74%);
}
.errors{ 
  display: flex;
  flex-direction: column;
  color: yellow;
}
.timer {
  min-height: 40px;
  background-color: red;
  color: white;
  /* margin: 150px; */
}
.text {
  margin-left: 50px;
  margin-bottom: 50px;
  letter-spacing: 3px;
  overflow: scroll;
  /* margin: 50px; */
  display: flex;
  /* font-family: 'Henny Penny', cursive; */
  font-family: 'Short Stack', cursive;

  height: 40vh;
  width: 100vw;
  /* margin: 500px; */
  font-size: 30px;
   white-space: pre-wrap;
   text-align: left;

}
.parameter {
  color: pink;
}
.highlight {
  font-size: 30px;
  background-color:rgb(40, 50, 180);
  opacity: 1;
  border: rgb(40, 50, 180) solid 5px;
}
.error {
  font-size: 30px;
  opacity: 1;
  border: red solid 5px;
  background-color: red;
}

.App {
  /* background-image: image('./bg-morning'); */
  display: flex;    
  flex-direction: column;
  background-color: #2d3436;
  background-image: linear-gradient(315deg, #2d3436 0%, #000000 74%);
  text-align: center;
  height: 100vh;
  width: 100vw;
  color: white;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}
.punctuation {
  color: green;
}

.operator {
  color: purple;
}
.keyword {
  color: red;
}

.function {
  color: orange;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
